<template>
    <div class="event" :class="{show : isOpen}">
        <p>{{ text }}</p>
        <button @click="isOpen=false">X</button>
    </div>
</template>
<script>
export default {
    name: "EventComponent",
    props: {
        text: String
    },
    data () {
        return {
            isOpen: true
        }
    }
}
</script>
<style>
    .event {
        background: #444;
        padding: 10px 20px;
        color: #fff;
        text-align: center;
        font-size: small;
        display: none;
        justify-content: space-between;
        align-items: center;
    }

    .show {
        display: flex;
    }

    .event p {
        margin: 0;
    }
    .event button {
        margin: 0;
    }
</style>